/* ----------------- 1. Base Styling ----------------- */

@font-face {
    font-family: 'cadosa';
    src: url('../fonts/SFUFuturaRegular.ttf');
}

@font-face {
    font-family: "uvnnhan";
    src: url('../fonts/UVNNhan_R.ttf');
}

@font-face {
    font-family: "uvnnhannang";
    src: url('../fonts/UVNNhanNang_R.ttf');
}

* {
    margin: 0;
    border: 0;
    padding: 0;
    font-size: 100%;
}

body {
    font-family: 'uvnnhannang';
}

h1, h2, h3, h4, h5, h6 {
    color: #262b2d;
    margin: 0 0 0.5em;
    font-weight: 700;
    line-height: 1.35em;
    font-family: "Montserrat", sans-serif;
}

