    }
}

@media (max-width: 768px) {
    h1, h2, h3, h4, h5, h6 {
        line-height: 1.15em;
    }
}

h1 {
    font-size: 45px;
}

@media (max-width: 1200px) {
    h1 {
        font-size: 36px;
    }
}

@media (max-width: 992px) {
