}

a {
    text-decoration: none;
    -webkit-transition: all 0.15s;
    -moz-transition: all 0.15s;
    transition: all 0.15s;
    color: inherit;
}

a:hover, a:focus, a:active {
    text-decoration: none;
    color: inherit;
}

html .row-fit {
    margin: 0;
}

html .row-fit [class*="col-"] {
    padding: 0;
}

html .row-fit-10 {
    margin: 0 -5px;
}

html .row-fit-10 [class*="col-"] {
    padding: 0 5px;
}

html .row-fit-30 {
    margin: 0 -15px;
}

html .row-fit-30 [class*="col-"] {
    padding: 0 15px;
}

.container-fluid {
    max-width: 1920px;
    padding-left: 7%;
    padding-right: 7%;
}

.no-padding {
    padding: 0 !important;
}

.no-margin {
    margin: 0 !important;
}

.no-select {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
}

.align-left {
