}

.align-center {
    text-align: center;
}

.align-right {
    text-align: right;
}

.clean-list {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

.clean-list li {
    list-style-type: none;
}

.content-wrapper {
    padding: 0;
}

