/* -------- Scroll Top Btn -------- */

.scroll-top-wrapper {
    -webkit-transition: all 0.3s cubic-bezier(0.455, 0.03, 0.515, 0.955);
    -moz-transition: all 0.3s cubic-bezier(0.455, 0.03, 0.515, 0.955);
    transition: all 0.3s cubic-bezier(0.455, 0.03, 0.515, 0.955);
    -webkit-transform-origin: 50% 100%;
    -ms-transform-origin: 50% 100%;
    transform-origin: 50% 100%;
    font-family: "Montserrat", sans-serif;
    display: inline-block;
    position: fixed;
    bottom: 20px;
    right: 30px;
    z-index: 99;
    opacity: 0;
    -webkit-transform: scale(0);
    -ms-transform: scale(0);
    transform: scale(0);
}

.scroll-top-wrapper.visible {
    opacity: 1;
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1);
}

.scroll-top-wrapper .scroll-top-page, .scroll-top-wrapper .scroll-top-article {
    -webkit-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.15);
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.15);
    -webkit-transition: all 0.3s cubic-bezier(0.455, 0.03, 0.515, 0.955);
    -moz-transition: all 0.3s cubic-bezier(0.455, 0.03, 0.515, 0.955);
    transition: all 0.3s cubic-bezier(0.455, 0.03, 0.515, 0.955);
    -webkit-transform-origin: 50% 100%;
    -ms-transform-origin: 50% 100%;
    transform-origin: 50% 100%;
    vertical-align: middle;
    display: block;
    background: #fafafa;
    text-align: center;
    border-radius: 50%;
    position: relative;
    line-height: 42px;
    font-weight: 300;
    overflow: hidden;
    cursor: pointer;
    font-size: 12px;
    color: #60686d;
    height: 42px;
    width: 42px;
}

.scroll-top-wrapper .scroll-top-page .placeholder, .scroll-top-wrapper .scroll-top-page .fa, .scroll-top-wrapper .scroll-top-article .placeholder, .scroll-top-wrapper .scroll-top-article .fa {
    -webkit-transition: all 0.25s cubic-bezier(0.455, 0.03, 0.515, 0.955);
    -moz-transition: all 0.25s cubic-bezier(0.455, 0.03, 0.515, 0.955);
    transition: all 0.25s cubic-bezier(0.455, 0.03, 0.515, 0.955);
}

.scroll-top-wrapper .scroll-top-page .placeholder, .scroll-top-wrapper .scroll-top-article .placeholder {
    color: #60686d;
    font-size: 11px;
    font-weight: 300;
    display: inline-block;
    opacity: 0;
}

.scroll-top-wrapper .scroll-top-page .fa, .scroll-top-wrapper .scroll-top-article .fa {
    line-height: 42px;
    font-size: 16px;
}

.scroll-top-wrapper .scroll-top-article {
    -webkit-transition-delay: 0.25s;
    -moz-transition-delay: 0.25s;
    transition-delay: 0.25s;
    visibility: hidden;
    -webkit-transform: translateY(100%);
    -ms-transform: translateY(100%);
    transform: translateY(100%);
    opacity: 0;
}

.scroll-top-wrapper .scroll-top-article .placeholder {
    opacity: 1;
}

.scroll-top-wrapper:hover .scroll-top-page {
    background: #f0f0f0;
}

.scroll-top-wrapper:hover .scroll-top-page .fa, .scroll-top-wrapper:hover .scroll-top-page .placeholder {
    -webkit-transform: translateY(-100%);
    -ms-transform: translateY(-100%);
    transform: translateY(-100%);
    opacity: 1;
}

.scroll-top-wrapper:hover .scroll-top-article {
    visibility: visible;
    -webkit-transform: translateY(-20%);
    -ms-transform: translateY(-20%);
    transform: translateY(-20%);
    opacity: 1;
}


